export const getMousePos = function(ev) {
  let posx = 0;
  let posy = 0;

  if (!ev) ev = window.event;
  if (ev.pageX || ev.pageY) 	{
    posx = ev.pageX;
    posy = ev.pageY;
  } else if (ev.clientX || ev.clientY) 	{
    posx = ev.clientX + document.body.scrollLeft + document.documentElement.scrollLeft;
    posy = ev.clientY + document.body.scrollTop + document.documentElement.scrollTop;
  }
  return { x : posx, y : posy };
};

export const moveElements = function(e) {
  e.preventDefault();
  let positions = {};

  var touches = e.changedTouches;
  Array.from(document.querySelectorAll('.square')).forEach(
    el => positions = getMousePos(touches ? touches[0] : e)
  );

  const elWrapper = document.querySelector(".flex-center");
  const screenWidth  = elWrapper.offsetWidth;
  const screenHeight = elWrapper.offsetHeight;
  const newPosX = ((positions.x * 100 / screenWidth) - 50) / 3;
  const newPosY = ((positions.y * 100 / screenHeight) - 50) / 3;

  Array.from(document.querySelectorAll('.square')).forEach(el => {
    el.style.transition = "none";
    el.style.transform = `translate(${newPosX}%, ${newPosY}%)`;
  });
};

export const endMove = function() {
  Array.from(document.querySelectorAll('.square')).forEach(el => {
    el.style.transition = "transform 300ms linear";
    el.style.transform = "translate(0%, 0%)";
  });
}


