import React, { Component } from 'react';
import styled from 'styled-components';
// language=SCSS prefix=dummy{ suffix=}
const AboutStyled = styled.section`
  box-sizing: border-box;
  position: relative;
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-wrap: wrap;
  margin: 0 auto;
  width: 100%;
  min-height: 100vh;
  background: #525252;
  overflow: hidden;
  
  @media (max-width: 600px) {
    padding-top: 50px;
  }
`;
// language=SCSS prefix=dummy{ suffix=}
const Title = styled.strong`
  position: absolute;
  box-sizing: border-box;
  align-self: flex-start;
  align-items: flex-start;
  left: 0;
  top: 0;
  font-family: Helvetica, Arial, Sans-Serif;
  font-family: 'Pacifico', cursive;
  font-weight: bold;
  font-size: 3rem;
  color: #525252;
  background: #f3f3f3;
  padding: .5rem;
  margin-top: -20px;
  margin-left: -20px;
  
  @media (max-width: 480px) {
    font-size: 2.5rem;
  }
`;
// language=SCSS prefix=dummy{ suffix=}
const Square = styled.article`
  position: relative;
  width: 300px;
  height: 150px;
  
  display: flex;
  justify-content: space-around;
  align-items: center;
  
  background-color: #08AEEA;
  background-image: linear-gradient(0deg, #08AEEA 0%, #2AF598 100%);

  transition: all 600ms linear;

  @media (max-width: 600px) {
    flex-direction: column;
    min-height: 80vh;
    height: inherit;
    padding-top: 100px;
  }
`;
// language=SCSS prefix=dummy{ suffix=}
const Content = styled.div`
  width: 60%;
  font-size: 1.5rem;
  color: #535353;
  padding-right: 1rem;
  opacity: 0;
  transition: all 600ms 600ms linear;
  
  font-family: Helvetica, Arial, Sans-Serif;
  font-family: 'Poiret One', cursive;
  font-weight: bold;
  line-height: 200%;
  
  em {
    background:#f3f3f3;
    color: #535353;
    padding: 5px;
  }

  @media (max-width: 1024px) {
    line-height: 150%;
    em {
      padding: 2px;
    }
  }

  @media (max-width: 850px) {
    font-size: 1rem;
    line-height: 200%;
  }
  
  @media (max-width: 600px) {
    width: 85%;
  }
`;
// language=SCSS prefix=dummy{ suffix=}
const ImageProfile = styled.div`
  overflow: hidden;
  width: 250px;
  height: 250px;
  border-radius: 50%;
  
  img {
    width: 100%;
    margin-top: -50px;
    opacity: 0;
    transition: all 600ms 600ms linear;
  }
  
  @media (max-width: 800px) {
    width: 200px;
    height: 200px;
  }

  @media (max-width: 600px) {
    min-height: 200px;
  }
`;

class About extends Component {

  componentDidMount() {
    setTimeout(() => {
      document.querySelector('.about-square').style.width = '80%';
      document.querySelector('.about-square').style.minHeight = '80vh';
      document.querySelector('.about-square-content').style.opacity = '1';
      document.querySelector('.about-square img').style.opacity = '1';
    }, 200);
  }

  render() {
    return (
      <AboutStyled id="Sobremi">
        <Square className="about-square">
          <Title>Sobre mi</Title>
          <ImageProfile>
            <img src="/img/miguel-cast-perfil.png" alt="Miguel Cast Developer"/>
          </ImageProfile>
          <Content className="about-square-content">
            <p>
              !Hola¡ mi nombre es <em>Miguel Castrill&oacute;n</em>, soy un profesional del desarrollo <em>web</em> y me encanta la <em>fotografía</em>.
              Vivo en la ciudad de <em>Medell&iacute;n</em> y estoy trabajando actualmente en proyectos para Cognox S.A.S.
            </p>
            <p>
              Tengo un conjunto de diversas habilidades, que van desde el diseño al <em>HTML + CSS + Javascript</em> (JQuery, ReactJs y NodeJs),
              todo un recorrido con <em>PHP</em>(Laravel y Phalcon), desarrollo personalizado en Joomla, Wordpress y Drupal, y administración de servidores.
            </p>
          </Content>
        </Square>
      </AboutStyled>
    );
  }
}

export default About;


