import React, { Component } from 'react';
import styled from 'styled-components';
import Item from './Item';
// language=SCSS prefix=dummy{ suffix=}
const FontsIcons = styled.div`
  @font-face {
    font-family: "Flaticon";
    src: url("./fonts/Flaticon.eot");
    src: url("./fonts/Flaticon.eot?#iefix") format("embedded-opentype"),
         url("./fonts/Flaticon.woff") format("woff"),
         url("./fonts/Flaticon.ttf") format("truetype"),
         url("./fonts/Flaticon.svg#Flaticon") format("svg");
    font-weight: normal;
    font-style: normal;
  }
  
  @media screen and (-webkit-min-device-pixel-ratio:0) {
    @font-face {
      font-family: "Flaticon";
      src: url("./fonts/Flaticon.svg#Flaticon") format("svg");
    }
  }
  
  [class^="flaticon-"]:before, [class*=" flaticon-"]:before,
  [class^="flaticon-"]:after, [class*=" flaticon-"]:after {   
    font-family: Flaticon;
          font-size: 3rem;
    font-style: normal;
    color: #DADADA;
  }
  
  .flaticon-github-logo:before { content: "\\f100"; }
  .flaticon-flickr-circle-logo:before { content: "\\f101"; }
  .flaticon-twitter-logo-button:before { content: "\\f102"; }
  .flaticon-facebook-logo-button:before { content: "\\f103"; }
`;

class Social extends Component
{
  render() {
    return (
      <FontsIcons>
        <Item
          url="https://www.facebook.com/miguel.cast.dev/"
          name="facebook-logo-button"
        />
        <Item
          url="https://twitter.com/miguel__cast"
          name="twitter-logo-button"
        />
        <Item
          url="https://www.flickr.com/miguelcast"
          name="flickr-circle-logo"
        />
        <Item
          url="https://github.com/miguelcast"
          name="github-logo"
        />
      </FontsIcons>
    );
  }
}

export default Social;



