import React, { Component } from 'react';
import styled from 'styled-components';

// language=SCSS prefix=dummy{ suffix=}
const MenuStyled = styled.nav`
  box-sizing: border-box;
  position: relative;
  display: flex;
  justify-content: space-around;
  align-items: center;
  height: 55px;
  padding: 10px;
`;
// language=SCSS prefix=dummy{ suffix=}
const Item = styled.a`
  position: relative;
  display: inline-block;
  padding: 5px;
  padding-left: 15px;
  
  font-family: Helvetica, Arial, Sans-Serif;
  font-family: 'Poiret One', cursive;
  font-weight: bold;
  text-decoration: none;
  color: white;

  @media (max-width: 400px) {
    padding-left: 8px;
  }
  
  &:hover {
    > span {
      height: 35px;
      background-image: linear-gradient(to right, #F05A28 0%, #E80A89 100%);
    }
    
    span span {
      background: transparent;
    }
  }
`;
// language=SCSS prefix=dummy{ suffix=}
const BackItem = styled.span`
  display: inline-block;
  width: 120px;
  height: 0px;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align:center;
  background-color: transparent;
  border: 1px solid transparent;
  border-image: linear-gradient(to right, #F05A28 0%, #E80A89 100%);
  border-image-slice: 1;
  transition: all 300ms ease-out;

  @media (max-width: 400px) {
    width: 90px;
  }
`;
// language=SCSS prefix=dummy{ suffix=}
const TitleItem = styled.span`
  padding: 5px;
  font-size: 1.3rem;
  color: #f3f3f3;
  transition: all 200ms ease-in;

  @media (max-width: 400px) {
    font-size: 1.1rem;
  }
`;

class Menu extends Component {
  renderItem(title) {
    return (
      <Item href={`#${title.replace(/\s+/g, '')}`}>
        <BackItem>
          <TitleItem>{title}</TitleItem>
        </BackItem>
      </Item>
    );
  }

  render() {
    return (
      <MenuStyled>
        {this.renderItem('Sobre mi')}
        {this.renderItem('Proyectos')}
        {this.renderItem('Contacto')}
      </MenuStyled>
    );
  }
}

export default Menu;



