import React, { Component } from 'react';
import styled, { keyframes } from 'styled-components';

import Project from './Project';
// language=SCSS prefix=dummy{ suffix=}
const MenuProjectAnimation = keyframes`
  0% {
    right: 50%;
    width: 50%
  }
  15% {
    right: 25%;
    width: 70%
  }
  40% {
    right: 5%;
    width: 20%
  }
  50% {
    right: 0;
    width: 0;
  }
  100% {
    right: 0;
    width: 100%
  }
`;
// language=SCSS prefix=dummy{ suffix=}
const SliderStyled = styled.div`
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  transition: all 600ms 300ms ease;
  
  @media (max-width: 600px) {
    flex-wrap: wrap;
  }
`;
// language=SCSS prefix=dummy{ suffix=}
const ListProject = styled.ul`
  width: 20%;
  list-style-type: none;
  
  @media (max-width: 600px) {
    padding-top: 20px;
    width: 100%;
  }
  
  
  li {
    position: relative;
    padding: 12px 0;
    color: #f3f3f3;
    font-family: Helvetica, Arial, Sans-Serif;
    font-family: 'Poiret One', cursive;
    font-weight: bold;
    cursor: pointer;
    
    @media (max-width: 600px) {
      display: inline-block;
      padding: 8px;
      margin: 5px;
    }

    span {
      position: absolute;
      display: block;
      margin-top: 5px;
      width: 50%;
      height: 3px;
      background-color: #21D4FD;
      background-image: linear-gradient(19deg, #21D4FD 0%, #B721FF 100%);
      transition: all 600ms ease;
    }
    
    &:hover span {
      width: 100%;
      animation: ${MenuProjectAnimation} 600ms forwards;
    }
    
  }
`;

class Slider extends Component {
  constructor(props) {
    super(props);
    this.state = {
      currentSlide: 0,
      mount: 1,
      opacity: 0,
    };
  }

  componentDidMount() {
    setTimeout(() => {
      this.setState({
        opacity: 1,
      });
    }, 300);
  }

  changeSlide = (slideIndex) => {
    this.setState(prevState => ({
      currentSlide: slideIndex,
      mount: 0,
    }));
  }

  render() {
    return (
      <SliderStyled style={{opacity: this.state.opacity}}>
        <ListProject>
          {this.props.slides.map((project, index) => (
            <li
              key={index}
              onMouseEnter={() => {
                this.changeSlide(index);
              }}
            >
              {project.name}
              <span />
            </li>
          ))}
        </ListProject>
        <Project
          {...this.props.slides[this.state.currentSlide]}
        />
      </SliderStyled>
    );
  }
}

export default Slider;



