import React, { PureComponent } from 'react';
import styled from 'styled-components';

import Social from '../social';
import Message from '../../svg/message.svg';

// language=SCSS prefix=dummy{ suffix=}
const ContactStyled = styled.section`
  box-sizing: border-box;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  margin: 0 auto;
  width: 100%;
  min-height: 100vh;
  background: #525252;
  overflow: hidden;
`;
// language=SCSS prefix=dummy{ suffix=}
const Title = styled.strong`
  position: absolute;
  box-sizing: border-box;
  align-self: flex-start;
  align-items: flex-start;
  left: 0;
  top: 0;
  font-family: Helvetica, Arial, Sans-Serif;
  font-family: 'Pacifico', cursive;
  font-weight: bold;
  font-size: 3rem;
  color: #525252;
  background: #f3f3f3;
  padding: .5rem;
  margin-top: -20px;
  margin-left: -20px;
  
  @media (max-width: 480px) {
    font-size: 2.5rem;
  }
`;
// language=SCSS prefix=dummy{ suffix=}
const Square = styled.article`
  position: relative;
  width: 80%;
  height: 80vh;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  align-items: center;
  flex-direction: column;
  background-color: #4158D0;
  background-image: linear-gradient(43deg, #4158D0 0%, #C850C0 46%, #FFCC70 100%);

  transition: all 600ms linear;

  @media (max-width: 400px) {
    width: 95%;
  }
`;

// language=SCSS prefix=dummy{ suffix=}
const ContactStrong = styled.strong`
  display: block;
  font-family: Helvetica, Arial, Sans-Serif;
  font-family: 'Poiret One', cursive;
  font-weight: bold;
  font-size: ${props => props.size};
  color: #f3f3f3;
  
  @media (max-width: 400px) {
    font-size: ${props => (parseInt(props.size) - .5)}rem;
  }
`;

// language=SCSS prefix=dummy{ suffix=}
const EmailTitle = styled.span`
  display: block;
  padding-top: 1rem;
  font-family: Helvetica, Arial, Sans-Serif;
  font-family: 'Poiret One', cursive;
  font-weight: bold;
  font-size: 1.5rem;
  color: #f3f3f3;
`;

// language=SCSS prefix=dummy{ suffix=}
const Quotes = styled.div`
  display: block;
  padding: 3rem 0;
  font-family: Helvetica, Arial, Sans-Serif;
  font-family: 'Poiret One', cursive;
  font-weight: bold;
  font-size: 1.2rem;
  color: #f3f3f3;
  
  @media (max-width: 400px) {
    font-size: 1rem;
  }
`;

// language=SCSS prefix=dummy{ suffix=}
const ContentEmail = styled.div`
  text-align:center;
  
  @media (max-width: 1200px) {
    padding-top: 80px;
  }
`;

class Contact extends PureComponent {
  render() {
    return (
      <ContactStyled id="Contacto">
        <Square className="about-square">
          <Title>Contacto</Title>
          <ContentEmail>
            <ContactStrong size="2rem">¿Puedo ayudarte? Envíame un mensaje.</ContactStrong>
            <EmailTitle>
              <img src={Message} alt="Email" style={{
                width: '48px', height: '48px',
                verticalAlign: 'middle', paddingRight: '10px'
              }} />
              miguel2@outlook.com
            </EmailTitle>
          </ContentEmail>
          <div style={{textAlign: 'center'}}>
            <ContactStrong size="3rem">Social</ContactStrong>
            <Social />
          </div>
          <Quotes>
            Solo HAZLO no lo pienses demasiado.
          </Quotes>
        </Square>
      </ContactStyled>
    );
  }
}

export default Contact;



