import React from 'react';
import styled, { keyframes } from 'styled-components';
import Icon from './Icon';

const redColor = 'rgba(244,67,54,1)';
const blueColor = 'rgba(33,150,243,1)';
// language=SCSS prefix=dummy{ suffix=}
const animation3D = keyframes`
  0% {
    text-shadow: 5px 4px ${redColor}, -5px -6px ${blueColor};
  }
  25% {
    text-shadow: -5px -6px ${redColor}, 5px 4px ${blueColor};
  }
  50% {
    text-shadow: 5px -4px ${redColor}, -8px 4px ${blueColor};
  }
  75% {
    text-shadow: -8px -4px ${redColor}, -5px -4px ${blueColor};
  }
  100% {
    text-shadow: -5px 0 ${redColor}, 5px -4px ${blueColor};
  }
`;
// language=SCSS prefix=dummy{ suffix=}
const ItemStyled = styled.a`
  display: inline-block;
  text-decoration: none;
  padding: 8px;
  margin-right: 5px;
  
  animation: ${animation3D} 200ms 10;
  
  &:hover {
    [class^="flaticon-"]:before, [class*=" flaticon-"]:before,
    [class^="flaticon-"]:after, [class*=" flaticon-"]:after {  
      animation: ${animation3D} 200ms infinite;
    }
  }
  
  @media (max-width: 400px) {
    padding: 3px;
    margin-right: 3px;
  }
`;

const Item = ({name, url}) => {
  return (
    <ItemStyled href={url} target="_blank" rel="noopener">
      <Icon name={name} />
    </ItemStyled>
  );
};

export default Item;



