import React, { Component } from 'react';
import styled, { keyframes } from 'styled-components';

import Laptop from '../Laptop';
// language=SCSS prefix=dummy{ suffix=}
const ProjectStyled = styled.div`
  position: relative;
  text-align: center;
  width: 80%;
  opacity: ${props => props.opacity ? 0 : 1};
  transform: translate(${props => props.opacity ? 100 : 0}px, 0); 
  transition: all 600ms ease;
  
  img {
    border-radius: 50%;
    transition: all 300ms ease;
  }
  
  @media (max-width: 600px) {
    width: 100%;
  }
`;
// language=SCSS prefix=dummy{ suffix=}
const Title = styled.div`
  position: relative;
  font-family: Helvetica, Arial, Sans-Serif;
  font-family: 'Pacifico', cursive;
  font-weight: bold;
  text-align: center;
  font-size: 2.5rem;
  color: #f3f3f3;
  transition: all 600ms ease;
  z-index: 10;

  @media (max-width: 800px) {
    font-size: 2rem;
  }
`;
// language=SCSS prefix=dummy{ suffix=}
const ContentImage = styled.div`
  position: relative;
  display: inline-block;
  width: 300px;
  height: 300px;
  z-index: 10;
  img {
    width: 300px;
    height: 300px;
  }
  
  @media (max-width: 800px) {
    transform: scale(.8);
  }
`;
// language=SCSS prefix=dummy{ suffix=}
const Tecnology = styled.span`
  display: inline-block;
  margin: 2px 5px 2px 2px;
  padding: 8px;
  background: #f3f3f3;
  color: #535353;
  font-family: Helvetica, Arial, Sans-Serif;
  font-family: 'Poiret One', cursive;
  font-weight: bold;
  font-size: 1.2rem;
  z-index: 10;
`;
// language=SCSS prefix=dummy{ suffix=}
const BackGradient = styled.div`
  position: absolute;
  top: 50%;
  width: 450px;
  height: 450px;
  left: 50%;
  z-index: 2;
  transform-origin: left top;
  transform: rotate(35deg) translate(-50%, -50%);
  background-color: #FF3CAC;
  background-image: linear-gradient(225deg, #FF3CAC 0%, #784BA0 50%, #2B86C5 100%);
  animation: ${keyframes`
    0% {
      transform: rotate(0deg) translate(-50%, -50%);
    }
    30% {
      transform: rotate(-55deg) translate(-50%, -50%);
    }
    70% {
      transform: rotate(55deg) translate(-50%, -50%);
    }
    100% {
      transform: rotate(0) translate(-50%, -50%);
    }
  `} 30s linear infinite;
  
  @media (max-width: 900px) {
    width: 380px;
    height: 380px;
  }
  
  @media (max-width: 800px) {
    width: 300px;
    height: 300px;
  }
`;

class Project extends Component {

  constructor(props) {
    super(props);
    this.state = {
      next: false,
      name: props.name,
      image: props.image,
      tecnology: props.tecnology,
    };
  }

  componentWillReceiveProps(nextProps) {
    const {name} = this.props;
    if(name !== nextProps.name) {
      this.setState({
        next: true,
      });
      setTimeout(() => {
        this.setState({
          next: false,
          name: nextProps.name,
          image: nextProps.image,
          tecnology: nextProps.tecnology,
        });
      }, 600);
    }
  }

  render() {
    const {name, image, tecnology = []} = this.state;
    return (
      <ProjectStyled opacity={this.state.next}>
        <Title>{name}</Title>
        <ContentImage>
          <Laptop image={image} />
        </ContentImage>
        <BackGradient />
        <div style={{position: 'relative', zIndex: '10'}}>
          {tecnology.map((tecno, index) => (
            <Tecnology key={index}>
              {tecno}
            </Tecnology>
          ))}
        </div>
      </ProjectStyled>
    );
  }
}

export default Project;



