import React, { Component } from 'react';
import styled from 'styled-components';
import Slider from '../Slider';
// language=SCSS prefix=dummy{ suffix=}
const ProjectsStyled = styled.section`
  box-sizing: border-box;
  position: relative;
  display: flex;
  justify-content: space-around;
  align-items: center;
  margin: 0 auto;
  width: 100%;
  min-height: 100vh;
  background: #525252;
  overflow: hidden;

  @media (max-width: 600px) {
    padding-top: 50px;
  }
`;
// language=SCSS prefix=dummy{ suffix=}
const Title = styled.strong`
  position: absolute;
  box-sizing: border-box;
  align-self: flex-start;
  align-items: flex-start;
  left: 0;
  top: 0;
  font-family: Helvetica, Arial, Sans-Serif;
  font-family: 'Pacifico', cursive;
  font-weight: bold;
  font-size: 3rem;
  color: #525252;
  background-color: #21D4FD;
  background-image: linear-gradient(19deg, #21D4FD 0%, #B721FF 100%);
  padding: .5rem;
  margin-top: -20px;
  margin-left: -20px;

  @media (max-width: 600px) {
    margin-left: 5%;
  }
  
  @media (max-width: 480px) {
    font-size: 2.5rem;
  }
`;
// language=SCSS prefix=dummy{ suffix=}
const Square = styled.article`
  position: relative;
  width: 300px;
  height: 150px;
  
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-wrap: wrap;

  transition: all 600ms linear;

  @media (max-width: 600px) {
    flex-direction: column;
    min-height: 80vh;
    height: inherit;
    padding-top: 50px;
    width: 100% !important;
  }
`;

const SLIDES = [
  {
    image: "/img/procesos.png",
    name: "Cognox Procesos",
    tecnology: [
      "React Js",
      "Next Js",
      "Styled-Components",
      "CSS3",
      "HTML",
    ]
  },
  {
    image: "/img/diagnostico.png",
    name: "Oviblue Diagnósticos",
    tecnology: [
      "React Js",
      "Create-react-app",
      "Bootstrap",
      "HTML",
      "Laravel",
      "MySql",
    ]
  },
  {
    image: "/img/icso.png",
    name: "ICSO",
    tecnology: [
      "Code QR",
      "PDF",
      "PHP",
      "MYSQL",
      "Bootstrap",
      "JQuery",
      "Laravel",
    ]
  },
  {
    image: "/img/orbitel-sales.png",
    name: "Orbitel Sales",
    tecnology: [
      "PHP",
      "MySql",
      "Joomla",
      "React Js",
      "Phalcon",
      "JQuery",
      "API Rest",
      "Soap",
    ]
  },
  {
    image: "/img/orbitel-store.png",
    name: "Orbitel Store",
    tecnology: [
      "CSS3",
      "Javascript",
      "PHP",
      "MYSQL",
      "Joomla",
      "JQuery",
      "Soap",
    ]
  },
  {
    image: "/img/gestion-compartida.png",
    name: "Gestion compartida",
    tecnology: [
      "PHP",
      "MYSQL",
      "Bootstrap",
      "JQuery",
      "Wordpress",
    ]
  },
  {
    image: "/img/pcfk.png",
    name: "PCFK - CARMEL - LOGUIN",
    tecnology: [
      "PHP",
      "SQL Server",
      "CSS",
      "Javascript",
      "Node Js",
      "AWS",
      "Squarespace",
    ]
  },
];

class Projects extends Component {

  componentDidMount() {
    setTimeout(() => {
      document.querySelector('.projects-square').style.width = '80%';
      document.querySelector('.projects-square').style.minHeight = '80vh';
    }, 200);
  }

  render() {
    return (
      <ProjectsStyled id="Proyectos">
        <Square className="projects-square">
          <Title>Proyectos</Title>
          <Slider slides={SLIDES} />
        </Square>
      </ProjectsStyled>
    );
  }
}

export default Projects;



